import React from 'react';
import App from '../App';
import { IoIosArrowForward } from 'react-icons/io';
import { Data } from '../common/Data';
import { Link } from 'react-router-dom';


class ExercisesNav extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            lessons: []
        };
    }


    render() {

        let list = this.state.lessons.map(function (lesson, index) {
            return (
                <li key={index} className="list-group-item li-nav">
                    <Link to={`/exercises/${lesson.id}`} className="nav-item">
                        {lesson.title}
                        <span className="itemRight">
                            <IoIosArrowForward />
                        </span>
                    </Link>
                </li>
            );
        });


        return (
            <div>
                <App />
                <div className="main">
                    <div className="card">
                        <ul className="list-group list-group-flush">
                            {list}
                        </ul>
                    </div>
                </div>
            </div>
        );
    }


    componentDidMount() {

        Data.get('lessons')
        .then((response) => {
            this.setState({
                lessons: response
            });
        });
    }

}

export default ExercisesNav;